@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,700;1,400;1,700&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:"Roboto";-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;-webkit-user-select:none;-ms-user-select:none;user-select:none}body{background-color:#eae2b7;overflow:hidden;color:#000}.divider{flex:auto}

Header{position:absolute;display:flex;width:100%;left:0;background-color:#fcbf49;padding-right:2em;padding-left:2em}Header.second{pointer-events:none;background-color:transparent;justify-content:center}Header.second *{pointer-events:auto}Header img{display:block;-webkit-user-drag:none;-khtml-user-drag:none;-moz-user-drag:none;-o-user-drag:none}Header img:hover{cursor:pointer}Header img.disabled{opacity:.3;cursor:default}
.Add,.Edit{display:flex;justify-content:center;align-items:center;position:absolute;width:100%;height:100%;top:0;left:0;z-index:1;font-size:1.2em}.Add-bg,.Edit-bg{position:absolute;width:100%;height:100%;background-color:rgba(0,0,0,.6)}.Add-bg:hover,.Edit-bg:hover{cursor:pointer}.Add-main,.Edit-main{position:absolute;display:flex;flex-direction:column;background-color:#eae2b7;border:.2em solid #000;border-radius:.8em;padding:2em;min-width:30%}.Add-main-inputs,.Edit-main-inputs{display:flex;flex-direction:column;margin-bottom:2em}.Add-main-inputs>*,.Edit-main-inputs>*{display:flex;flex-direction:row;justify-content:space-between;margin-bottom:.5em}.Add-main-inputs>*>label,.Edit-main-inputs>*>label{margin-right:2em}.Add-main-inputs>*>input,.Edit-main-inputs>*>input{background-color:rgba(0,0,0,0);box-shadow:none;border:none;border-bottom:.2em solid #000;font-size:1em}.Add-main-inputs>*>select,.Edit-main-inputs>*>select{background-color:rgba(0,0,0,0);box-shadow:none;border:none;border-bottom:.2em solid #000;font-size:1em}.Add-main-buttons,.Edit-main-buttons{display:flex;flex-direction:row;justify-content:space-evenly}.Add-main-buttons-delete,.Edit-main-buttons-delete{color:#dc1414}.Add-main-buttons>*:hover,.Edit-main-buttons>*:hover{cursor:pointer}
.TaskField{position:absolute;width:100%;top:3em;bottom:0;display:flex;flex-direction:row;justify-content:space-evenly;padding:1em}
.TaskGroup{display:flex;flex-direction:column;background-color:#d2cba4;padding:.5em;border:.2em solid #000;border-radius:.8em;margin-right:.5em;flex:1 1;font-size:1em}.TaskGroup>.TaskGroup-title{display:flex;flex-direction:row;justify-content:space-between;margin-bottom:1em}.TaskGroup>.TaskGroup-title>img{visibility:hidden;height:70%}.TaskGroup>.TaskGroup-title>img:hover{cursor:pointer}.TaskGroup>.TaskGroup-title:hover>img{visibility:visible}.TaskGroup>.TaskList-wrapper{display:flex;flex-direction:column;flex:1 1}
.TaskList{display:flex;flex-direction:column;background-color:#b3ad8c;padding:.5em;border:.2em solid #000;border-radius:.8em;margin-bottom:.5em}.TaskList>.TaskList-title{display:flex;flex-direction:row;justify-content:space-between;height:30px}.TaskList>.TaskList-title>h3>span{opacity:.4;font-size:.8em}.TaskList>.TaskList-title>img{visibility:hidden;height:70%}.TaskList>.TaskList-title>img:hover{cursor:pointer}.TaskList>.TaskList-title:hover>img{visibility:visible}.TaskList>.Task-wrapper{display:flex;flex-direction:column;flex:1 1}
.Task{display:flex;flex-direction:row;grid-gap:1em;gap:1em;margin-bottom:.2em}.Task.completed{text-decoration:line-through;font-style:italic;color:gray}.Task>img{height:50%}
